.activities-wrapper {
  display: flex;
  justify-content: space-around;
  &__first-col {
    max-width: 340px;
    &-title {
      color: #000;
      font-family: Stevie Sans;
      font-size: 42px;
      font-style: normal;
      font-weight: 600;
    }
    &-subtitle {
      color: #000;
      font-family: Stevie Sans;
      font-size: 28px;
      font-style: normal;
      font-weight: 600;
    }
    &-content {
      color: #000;
      font-family: Stevie Sans;
      font-size: 20px;
      font-style: normal;
      font-weight: 400;
    }
  }
}
